.search-section {
  margin-top: 100px;
  margin-bottom: 100px;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.catalog-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.icon {
  width: 18px;
  height: 18px;
}

.input-field {
  max-width: 224px;
  color: #b7b7b7;
