  border-radius: 8px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.gallery {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.gallery-section {
  margin-bottom: 20px;
}





:root {
  --primary-brand-color: #000;
  --primary-text-color: #b7b7b7;
}

header {
  background: var(--primary-brand-color);
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

body.is-open {
  overflow: hidden;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo_text {
  color: var(--primary-text-color);
}

.logo_img {
  width: 30px;
  height: 24px;
}

.navbar_item_link {
  text-transform: uppercase;
  color: var(--primary-text-color);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile_menu_container {
  margin: 0 auto;
}

.backdrop.visual_hidden {
  opacity: 0;
}

.backdrop {
  opacity: .2;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(255, 255, 255, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.visual_hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.mobile_menu_logo_img {
  width: 32px;
  height: 32px;
}

@media screen and (max-width: 767px) {
  .logo_text {
    display: none;
  }

  .header_menu_btn {
    text-transform: uppercase;
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .mobile_menu_container {
    background-color: var(--primary-brand-color);
    height: 100%;
    z-index: 100;
    width: calc(100% - 116px);
    border-radius: 0 8px 8px 0;
    padding: 20px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-100%);
  }

  .mobile_menu_container.is-open {
    transform: translate(0);
  }

  .navbar {
    margin-top: 40px;
  }

  .navbar_list {
    flex-direction: column;
    gap: 24px;
    padding-left: 0;
    display: flex;
  }
}

#icon_moon, #icon_sun {
  width: 8px;
  height: 8px;
}

.header_theme_switch {
  cursor: pointer;
  width: 38px;
  height: 16px;
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  border-radius: 194px;
  align-items: center;
  gap: 14px;
  padding: 5px;
  display: flex;
  position: relative;
}

#icon-moon, #icon-sun {
  width: 8px;
  height: 8px;
  fill: #fff;
}

@media screen and (min-width: 768px) {
  .header_container {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  .mobile_menu_logo_img {
    display: none;
  }

  .logo_img {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }

  .logo_text {
    font-size: 24px;
    font-weight: 400px;
    line-height: 1.16;
    display: inline-block;
  }

  .navbar_list {
    font-size: 14px;
    font-weight: 500px;
    gap: 32px;
    line-height: 1.15;
    display: flex;
  }

  .header_menu_btn {
    display: none;
  }

  .header_theme_switch {
    width: 54px;
    height: 20px;
    gap: 24px;
    margin-left: auto;
  }

  #icon-moon, #icon-sun {
    width: 11px;
    height: 11px;
  }
}

@media screen and (min-width: 1200px) {
  .header_container {
    padding-top: 6px;
